#result {
  background-color: white;
  height: 90%;
  width: 25%;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  color: black;
}
#result header h1 {
  font-weight: 200;
  font-size: 2.4rem;
}
#result section p {
  font-weight: 400;
  font-size: 1.5rem;
}
#result footer {
  height: 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#result footer button {
  background-color: transparent;
  border: solid 1px black;
  height: 3rem;
  width: 40%;
  font-weight: 400;
}